.movies-container .movies-list {
  position: relative;
  width: 19%;
  margin: 2.4rem 0.5%;
  min-width: 240px;
  max-width: 240px;
  max-height: 360px;
  border-radius: 0.4rem;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  overflow: hidden;
  box-shadow: 0.4rem 0.4rem 0.8rem 0rem #696672bf;
}
.movies-container > a {
  text-decoration: none;
}
.movie-poster {
  width: 100%;
  height: 100%;
  vertical-align: center;
  border-radius: 0.4rem;
}
.movie-data {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  padding: 2rem;
  border-radius: 0.4rem;
  background-color: #0a0a0ad9;
  opacity: 0;
  transition: 0.2s;
  overflow: hidden;
}
.movies-list:hover > .movie-data {
  opacity: 1;
}
.movie-data > .movie-title {
  padding: 0.8rem 0;
  margin: 0 0 0.8rem 0;
  border-bottom: 2px solid #ffffff;
  color: #ffffff;
  font-size: 1.8rem;
  font-weight: 700;
}
.movie-data > .movie-year {
  padding: 0 0 0.4rem 0;
  color: #ffffffe6;
  font-size: 1.4rem;
  font-weight: 500;
}
.movie-data > .movie-genres {
  display: flex;
  flex-wrap: wrap;
  padding: 0 0 0.4rem 0;
}
.movie-data > .movie-genres > li {
  padding: 0 0.8rem 0 0;
  color: #ffffffb3;
  font-size: 1.2rem;
  line-height: 2rem;
}
.movie-data > .movie-summary {
  width: 100%;
  height: 6.4rem;
  color: #ffffff73;
  font-size: 1.2rem;
  font-weight: 300;
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  word-wrap: break-word;
  line-height: 1.6rem;
}

@media (max-width: 450px) {
  .movies-container .movies-list {
    display: flex;
    flex-direction: column;
    width: 100%;
    min-width: unset;
    max-width: unset;
    max-height: unset;
    padding: 2rem;
    margin: 1.2rem 0.5%;
    background-color: #ffffff;
  }
  .movie-poster {
    width: auto;
    height: 100%;
    vertical-align: center;
    border-radius: 0.4rem;
  }
  .movie-data {
    position: unset;
    width: 100%;
    height: 100%;
    padding: 2rem 1.2rem;
    margin: 1.2rem 0 0 0;
    background-color: #f6f6f6;
    opacity: 1;
  }
  .movie-data > .movie-title {
    color: #272727cc;
    border-bottom-color: #272727cc;
  }
  .movie-data > .movie-year,
  .movie-data > .movie-genres > li,
  .movie-data > .movie-summary {
    color: #272727b3;
  }
  .movie-data > .movie-genres > li,
  .movie-data > .movie-summary {
    font-weight: 600;
  }
}

@charset "UTF-8";
/* weight: 100 ~ 900 */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Raleway', sans-serif;
}

html,
body,
#root {
  position: relative;
  height: 100%;
  background-color: #f9f7ff;
}
html {
  font-size: 10px;
}
ul {
  list-style: none;
}

/* scroll */
body::-webkit-scrollbar {
  width: 1.2rem;
}
body::-webkit-scrollbar-track {
  background-color: transparent;
}
body::-webkit-scrollbar-thumb {
  border: transparent 0.4rem solid;
  border-radius: 2rem;
  background-color: #97979766;
  background-clip: padding-box;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
body::-webkit-scrollbar-thumb:hover {
  background-color: #979797cc;
}
body::-webkit-scrollbar-button {
  width: 0;
  height: 0;
}

/* loader */
.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.loader > .loader-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.loader > .loader-box > .loader-text {
  margin: 1.6rem 0 0 0;
  color: #444444;
  font-size: 2rem;
  letter-spacing: -0.1rem;
}
.loader > .loader-box > .loader-spin {
  position: relative;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background: #4f3399;
  background: linear-gradient(to right, #4f3399 10%, rgba(255, 255, 255, 0) 42%);
  animation: spin 1.4s infinite linear;
  transform: translateZ(0);
}
.loader > .loader-box > .loader-spin:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 50%;
  background: #4f3399;
  border-radius: 100% 0 0 0;
}
.loader > .loader-box > .loader-spin:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 80%;
  height: 80%;
  margin: auto;
  border-radius: 50%;
  background: #f9f7ff;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/* Loader Spin Source : https://projects.lukehaas.me/css-loaders/ */

